    backClick(){
               // history.back();
                 window.location.href="#/commine";

            },
       winList(){
           this.http.post("/home/user/win_record/")
           .then(res=>{
               if(res.data.code==0){

               }else if(res.data.code==1){
                   this.win=res.data.msg;
               }
           })
       },
       isloginList(){
            this.http.post("/home/login/is_login/")
            .then(res=>{  
               if(res.data.code==0){
                    window.location.href="#/";
                }else if(res.data.code==1){
                    window.location.href="#/comwin";
                }
           })
